import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import './App.css';
import About from './About.js';
import ProjectList from './ProjectList.js';
import Footer from './Footer.js';
import CssBaseline from '@material-ui/core/CssBaseline';
import FavoriteBorder from '@material-ui/icons/FavoriteBorder';

const heartStyle = {
  position: 'relative',
  top: '6px'
};

class App extends Component {
  render() {
    return (
      <React.Fragment>
        <CssBaseline />
        <div className="App">
          <About/>
          <ProjectList />
          <Footer/>
          <div class="footer">
            <p>&#169; {(new Date()).getFullYear()} <a href="mailto:conlonnovak+website@cmu.edu">Conlon Novak</a>. All rights reserved. Made with <FavoriteBorder color="secondary" style={heartStyle}/> using <a href="https://reactjs.org/">React</a> and <a href="https://material-ui.com/">Material-UI</a>.</p> 
          </div>
        </div>
    </React.Fragment>
    );
  }
}

export default App;